
<template>
  <div>
    <div class="view">
      <img src="../assets/image/icon_live_delete_white.png" @click="close()" class="live_delete" />
      <div class="comment">
        <ul class="comment-ul">
          <li class="comment-li" v-for="(item, index) in danmuList" :key="index">
            <div>
              <span class="username">{{ decode(item.userName) }}</span>
              <span class="comment-ct">{{ decode(item.msg) }}</span>
            </div>
          </li>
        </ul>
      </div>
      <div class="footer-bar flex-space-between">
        <div class="shop-group">
          <img src="../assets/image/shop.png" class="shop-img" @click="goods.showList = true" />
          <div class="goods-card" v-if="goods.current">
            <div class="flex-space-between" @click="chooseGoods(goods.current)">
              <img :src="goods.current.spuPicUrl" class="card-l" />
              <div class="card-r">
                <p class="goods-name van-multi-ellipsis--l2">{{ goods.current.spuName }}</p>
                <p class="goods-price">¥{{ goods.current.skuPrice }}</p>
              </div>
            </div>
          </div>
        </div>
        <div class="footer-input" @click="showDmInput()">跟管家聊点什么呗～</div>
        <!-- @click="showCoupon(1)"-->
        <div class="footer-icon" @click="coupon.showList = true">
          <img src="../assets/image/icon_coupon.png" class="icon-sm" />
          <p class="icon_txt">领券</p>
        </div>
        <div class="footer-icon">
          <img src="../assets/image/nav_icon_share.png" class="icon-sm" />
          <p class="icon_txt">分享</p>
        </div>
        <div class="footer-icon txt-center wid-r-sm">
          <div class="canvas-div">
            <canvas id="thumsCanvas" width="200" height="400" class="canvas"></canvas>
            <div @click="startBtn" class="ax-btn">
              <img :src="heart" alt class="icon-red" />
              <p class="icon_txt_red">1899</p>
            </div>
          </div>
        </div>
      </div>

      <van-popup v-model="input.show" position="bottom" :style="{ height: '60px', bottom: bottom }">
        <div class="footer-bar-ipt wid-100">
          <div class="flex-space-between white-bg ft-c">
            <input
              ref="inputVal"
              class="footer-input"
              placeholder="跟管家聊点什么呗～"
              v-model="input.value"
            />
            <button class="btn" @click="sendDanmu">发送</button>
          </div>
        </div>
      </van-popup>
    </div>
    <!--优惠券弹窗-->
    <div
      class="coupon animated"
      :class="{ bounceInDown: coupon.show === 1, bounceOut: coupon.show === 0 }"
    >
      <div class="coupon-ct">
        <img src="../assets/image/close.png" class="coupon-close" @click="showCoupon(0)" />
        <div class="coupon-money">
          <div class="flex-center">
            <span class="money-unit">¥</span>
            <span class="money-big">100</span>
          </div>
          <p class="red-big">优惠券</p>
          <p class="gray-sm">满59减10使用</p>
        </div>
        <div class="coupon-footer flex-center">
          <button class="getcoupon">立即领取</button>
        </div>
      </div>
    </div>
    <!--商品详情-->
    <van-popup round v-model="goods.showBuy" closeable position="bottom">
      <div class="content">
        <div class="sheet-ct-top flex-item-al">
          <img :src="goods.chooseed.spuPicUrl" class="sheet-goodsImg" />
          <div class="mar-top-15 sheet-r">
            <p class="goods-price">¥{{ goods.chooseed.skuPrice }}</p>
            <p class="goods-kc">库存{{ goods.chooseed.storeNumber }}件</p>
          </div>
        </div>
        <van-divider
          :style="{
            color: '#E5E5E5',
            borderColor: '#E5E5E5',
            padding: '0 15px',
          }"
        />
        <div class="sheet-ct-c flex-space-between">
          <span class="gmsl-span">购买数量</span>
          <div class="flex-center">
            <span class="jian flex-center" @click="jian">
              <img src="../assets/image/jian.png" class="img-icon" />
            </span>
            <label class="flex-center num">{{ buyNum }}</label>
            <span class="jia flex-center" @click="jia">
              <img src="../assets/image/jia.png" class="img-icon" />
            </span>
          </div>
        </div>
        <div class="sheet-ct-footer flex-center">
          <button class="add-cart">加入购物车</button>
          <button class="buy">立即购买</button>
        </div>
      </div>
    </van-popup>
    <!--商品列表-->
    <van-popup round v-model="goods.showList" closeable position="bottom">
      <p class="title txt-center">直播商品</p>
      <div class="content-auto" v-if="goods.list.length > 0">
        <div class="card-item" v-for="(item, idx) in goods.list" :key="idx">
          <div class="flex-space-round">
            <img :src="item.spuPicUrl" class="card-l-b" />
            <div class="card-r flex-item-al wid-50">
              <div>
                <p class="goods-name-big van-multi-ellipsis--l2">{{ item.spuName }}</p>
                <p class="goods-brand van-multi-ellipsis--l2">{{ item.shopName }}</p>
                <p class="goods-price">¥{{ item.skuPrice }}</p>
              </div>
            </div>
            <button class="goBug" @click="chooseGoods(item)">立即购买</button>
          </div>
        </div>
      </div>
      <div class="no-data" v-else>
        <img src="../assets/image/s.png" class="s-img flex-center" />
        <p class="tips txt-center">请稍等～商品正在上架哦</p>
      </div>
    </van-popup>
    <!--优惠券列表-->
    <van-popup round v-model="coupon.showList" position="bottom">
      <van-icon name="arrow-left" class="arrow-left" @click="coupon.showList = false" />

      <p class="title txt-center">优惠券</p>
      <div class="content-auto" v-if="coupon.list.length > 0">
        <template v-for="(item, idx) in coupon.list">
          <div
            class="coupon-item"
            :class="{
              'coupon-over': item.isPicked == 1,
              'coupon-ok': item.isPicked == 0,
            }"
            :key="idx"
          >
            <div class="flex-space-round wid-100">
              <div class="coupon-price">
                <div class="half-hig">
                  <span class="unit">¥</span>
                  <span class="coupon-price-num">{{ item.couponTotal }}</span>
                </div>
                <div class="coupon-fw">
                  <span>适用范围</span>
                  <van-icon name="arrow-down" />
                </div>
              </div>
              <div class="wid-70 coupon-con">
                <p class="coupon-name-big van-ellipsis">{{ item.shopName }}</p>
                <p class="coupon-time van-ellipsis">{{ item.name }}</p>
                <p
                  class="coupon-time van-ellipsis mar-top-15"
                  v-if="item.effType == 1"
                >{{ item.effBeginDay }}-{{ item.effEndDay }}</p>
                <p class="coupon-time van-ellipsis mar-top-15" v-else>领取后{{ item.effDays }}天生效</p>
                <button class="coupon-btn" v-if="item.isPicked == 1">已领取</button>
                <button class="coupon-btn" v-else @click="pickup(item)">立即领取</button>
              </div>
            </div>
          </div>
        </template>
      </div>
      <div class="no-data" v-else>
        <img src="../assets/image/s.png" class="s-img flex-center" />
        <p class="tips txt-center">暂时没有优惠券哦～</p>
      </div>
    </van-popup>

    <!--用户设置-->
    <van-popup round v-model="setUserShow" position="bottom" class="popup-height-38 set">
      <div class="set-ct">
        <div class="set-top">
          <div @click="setModal(1)">禁言</div>
        </div>
        <div class="set-footer" @click="setUserShow = false">取消</div>
      </div>
    </van-popup>
    <!--弹窗提示-->
    <zb-dialog ref="zbDialog"></zb-dialog>
  </div>
</template>

<script>
import {
  getLivePushedGoodsDetailList,
  addLiveInteractInfo,
  queryPickupList,
  pickup
} from "../assets/js/api";
import store from "../../store";
import xin from "../assets/image/heart_white.png";
import xin_red from "../assets/image/heart_red.png";

import { Button, Popup, Divider, Icon, Dialog, Toast } from "vant";
import ThumbsUpAni from "../components/dianzan/js/canvas";
import ZbDialog from "../components/ZbDialog";
import { Base64 } from "js-base64";
export default {
  name: "Home",
  components: {
    ZbDialog,
    [Button.name]: Button,
    [Popup.name]: Popup,
    [Divider.name]: Divider,
    [Icon.name]: Icon,
    [Dialog.Component.name]: Dialog.Component
  },
  watch: {
    "coupon.showList"(value) {
      if (value) {
        this.queryPickupList();
      }
    },
    "goods.showList"(value) {
      if (value) {
        this.getLivePushedGoodsDetailList();
      }
    },
    "goods.showBuy"(value) {
      if (value) {
        this.goods.showList = false;
      }
    }
  },
  data() {
    return {
      bottom: "0px",
      danmuList: store.state.store_live.live.danmuList,
      thumbsUpAni: null, // 储蓄创建构造函数
      heart: xin,
      xin_red: xin_red,
      showSet: false,
      roomId: store.state.store_live.live.roomId,
      buyNum: 1, // 购买数量
      setUserShow: false,
      goods: {
        current: null,
        chooseed: {},
        showList: false,
        list: [],
        showBuy: false
      },
      coupon: {
        list: [],
        showList: false,
        show: false
      },
      input: {
        show: false,
        value: ""
      }
    };
  },
  created() {
    console.log(store);
    // console.log('store.state.store_live.live.roomId'+store.store_live.state.live.roomId)
    this.getLivePushedGoodsDetailList();
  },
  mounted() {
    //键盘监听
    this.keyboards();
    // 创建构造函数
    this.thumbsUpAni = new ThumbsUpAni();
    this.thumbsUpAni.canvasId = "thumsCanvas";
    this.thumbsUpAni.reload();
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      switch (from.path) {
        case "/live/list":
          vm.$live.join();
          break;
        default:
          vm.$live.setDisplayMode(0);
          break;
      }
    });
  },
  beforeRouteLeave(to, from, next) {
    switch (to.path) {
      case "/live/over":
        this.$live.setDisplayMode(1);
        break;
      default:
        this.$live.quit();
        break;
    }
    next();
  },
  methods: {
    //选择弹幕
    choseItem(item) {
      this.currentOpUser = {
        userId: item.userId
      };
      this.setUserShow = true;
    },
    setModal(type, userId) {
      this.userId = userId;
      this.type = type;
      this.setUserShow = false;
      var txt = "";
      switch (type) {
        case 1:
          txt = "确认将该用户禁言？";
          break;
        case 2:
          txt = "确认解除禁言吗？";
          break;
      }
      this.$refs.zbDialog.content = txt;
      this.$refs.zbDialog.showDialog = true;
    },
    //显示输入弹幕
    showDmInput() {
      this.input.show = true;
      this.$nextTick(function() {
        //DOM 更新了
        this.$refs.inputVal.focus();
      });
    },
    //键盘事件
    keyboards() {
      var that = this;
      //监听软键盘弹起
      window.addEventListener("native.keyboardshow", keyboardShowHandler);
      function keyboardShowHandler(e) {
        setTimeout(function() {
          that.bottom = e.keyboardHeight + 90 + "px";
        }, 300);
      }
      //监听软键盘收起
      window.addEventListener("native.keyboardhide", keyboardHideHandler);
      function keyboardHideHandler() {
        setTimeout(function() {
          that.bottom = 0 + "px";
        }, 300);
      }
    },

    //获取已经推送的商品列表
    getLivePushedGoodsDetailList() {
      getLivePushedGoodsDetailList(this.roomId).then(res => {
        if (res.resultCode === "0" && res.data) {
          this.goods.list = res.data.list;
          if (this.goods.list.length > 0) {
            this.goods.current = this.goods.list[0];
          } else {
            this.goods.current = null;
          }
        }
      });
    },
    //选中商品购买
    chooseGoods(item) {
      this.goods.chooseed = item;
      this.goods.showBuy = true;
    },
    //优惠券列表 可领用的优惠券查询（业主APP）
    queryPickupList() {
      let params = {
        ownerCode: store.state.store_live.ownerCode,
        areaCode: store.state.store_live.areaCode
      };
      queryPickupList(params).then(res => {
        if (res.resultCode === "0" && res.data) {
          this.coupon.list = res.data.date;
        }
      });
    },
    //优惠券领取
    pickup(data) {
      let params = {
        id: data.id,
        ownerCode: store.state.store_live.ownerCode,
        areaCode: store.state.store_live.areaCode
      };
      pickup(params).then(res => {
        if (res.resultCode === "0") {
          data.isPicked = true;
        }
      });
    },
    // 交互信息保存 1-转发，2-点赞
    addLiveInteractInfo() {
      const params = {
        roomId: this.roomId,
        interactType: 2,
        createdBy: store.state.store_live.ownerCode
      };
      addLiveInteractInfo(params).then(res => {
        if (res.resultCode != "0") {
          Toast(res.msg);
        }
      });
    },
    //显示优惠券
    showCoupon(type) {
      this.coupon.show = type;
    },
    //开始按钮
    startBtn() {
      this.heart = this.xin_red;
      this.thumbsUpAni.start();
      this.addLiveInteractInfo();
    },
    // 发弹幕
    sendDanmu() {
      var _this = this;
      if (!_this.input.value) {
        return false;
      }
      // eslint-disable-next-line no-undef
      HisenseLive.sendDanmu(
        Base64.encode(_this.input.value),
        function(info) {
          _this.input.value = "";
          _this.input.show = false;
          Toast("sendDanmu success:" + info);
        },
        function(error) {
          Toast("sendDanmu error:" + error);
        }
      );
    },
    //退出
    close() {
      this.$router.back();
    },
    // 购买数量
    jia() {
      if (this.buyNum === this.goods.chooseed.storeNumber) {
        Toast("超出库存");
        return;
      } else {
        this.buyNum++;
      }
    },
    // 购买数量
    jian() {
      if (this.buyNum === 1) {
        Toast("最低购买1件");
        return;
      } else {
        this.buyNum--;
      }
    },
    //解码
    decode(value) {
      try {
        return Base64.decode(value);
      } catch (e) {
        return value;
      }
    }
  }
};
</script>

<style scoped src="../assets/css/home.css">
</style>
