<template>
  <div>
    <van-nav-bar :fixed="true" title="直播列表" left-text left-arrow @click-left="onClickLeft" />
    <div class="list-ct">
      <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
        <!--<van-cell v-for="item in list" :key="item" >-->

        <!--</van-cell>-->
        <div
          class="list-item flex-space-between align-items-start"
          v-for="(item, idx) in list"
          :key="idx"
        >
          <div class="list-item-l">
            <img :src="item.imgUrl" class="list-item-img" />
            <div class="tag-group">
              <span class="live-tag">
                {{
                item.isAlive ? "直播中" : "已结束"
                }}
              </span>
              <span class="live-num">1832</span>
            </div>
            <div class="canvas-div">
              <div class="ax-btn txt-center">
                <img src="../assets/image/heart_l.jpg" alt class="icon-l" />
                <img src="../assets/image/heart_red.png" alt class="icon-red" />
                <p class="icon_txt_red">1899</p>
              </div>
            </div>
          </div>
          <div class="list-item-r">
            <p class="van-multi-ellipsis--l3 live-title">{{ item.liveTitle }}</p>
            <button v-if="item.isAlive" class="btn-link" @click="jumpLive(item)">查看直播</button>
          </div>
        </div>
      </van-list>
    </div>
  </div>
</template>

<script>
import { getLiveListByAreaCode } from "../assets/js/api";

import { NavBar, List, Cell } from "vant";
import store from "../../store";
export default {
  name: "List",
  components: { [NavBar.name]: NavBar, [List.name]: List, [Cell.name]: Cell },
  watch: {},
  data() {
    return {
      list: [],
      loading: false,
      finished: false
    };
  },
  created() {
    this.getLiveListByAreaCode();
  },
  mounted() {},
  methods: {
    //返回
    onClickLeft() {
      window.history.length > 1 ? this.$router.go(-1) : this.$router.push("/");
    },
    //跳转直播页面
    jumpLive(item) {
      const live = {
        status: 0,
        roomId: item.roomId
      };
      store.commit("updateLive", live);
      this.$router.push({ path: "/live/home" });
    },
    //获取直播
    getLiveListByAreaCode() {
      const params = {
        areaCode: store.state.store_live.areaCode,
        ownerCode: store.state.store_live.ownerCode
      };
      getLiveListByAreaCode(params).then(res => {
        if (res.resultCode == "0" && res.data) {
          this.list = res.data;
        }
      });
    },
    onLoad() {
      // 异步更新数据
      // setTimeout 仅做示例，真实场景中一般为 ajax 请求
      // setTimeout(() => {
      //   for (let i = 0; i < 10; i++) {
      //     this.list.push({
      //       isCheck:false
      //     });
      //   }
      //   // 加载状态结束
      //   this.loading = false;
      //
      //   // 数据全部加载完成
      //   if (this.list.length >= 40) {
      //     this.finished = true;
      //   }
      // }, 1000);
    }
  }
};
</script>

