import api from "../utils/httpclient";

/**
 * 根据商品信息获取关联的直播信息
 */
export function getLiveRecordByGoods(spuCode) {
  return api({
    url: "xwj-commerce-live/liveMng/getLiveRecordByGoods?spuCode=" + spuCode,
    method: "get",
  });
}

/**
 * 直播已推送商品信息--业主端商品列表
 */
export function getLivePushedGoodsDetailList(roomId) {
  return api({
    url: "xwj-commerce-live/liveMng/getLivePushedGoodsDetailList?roomId="+roomId,
    method: "get",
  });
}
/**
 * 优惠券列表
 */
export function queryPickupList(data) {
  return api({
    url: "xwj-commerce-coupon/coupon/queryPickupList",
    method: "get",
    params: data
  });
}
/**
 * 优惠券领取
 */
export function pickup(data) {
  return api({
    url: "xwj-commerce-coupon/coupon/pickup",
    method: "post",
     data
  });
}
/**
 * 本小区下直播列表
 */
export function getLiveListByAreaCode(data) {
  return api({
    url: "xwj-commerce-live/liveMng/getLiveListByAreaCode?",
    method: "get",
    params:data
  });
}
/**
 *交互信息保存
 */
export function addLiveInteractInfo(data) {
  return api({
    url: "xwj-commerce-live/liveMng/addLiveInteractInfo",
    method: "post",
    data
  });
}
/**
 * post测试
 */
export function login(mobile, code) {
  return api({
    url: "xwj-commerce-live/auth/user/login",
    method: "post",
    data: {
      mobile: mobile,
      verifycode: code,
    },
  });
}
